<template>
  <div class="top">
    <div class="info_wrapper">
      <div class="name">
        <span>姓名</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入姓名" v-model="data_hu.username"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>联系电话</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入代理绑定的手机号码" v-model="data_hu.mobile"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>微信号</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入微信号" v-model="data_hu.wx_number"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>身份证号</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入身份证号" v-model="data_hu.code_id"/>
    </div>

    <div class="info_wrapper" @click="yiji=true">
      <div class="name">
        <span>代理分成比</span>
        <span class="red">*</span>
      </div>
      <div class="weizhi">
        <span>{{data_hu.one_level}}%</span>
      </div>
    </div>
    <!-- 提交 -->

    <div class="submit">
      <button class="btn" @click="getsetin()">提交</button>
    </div>

    <!-- 一级分成比 -->
    <van-popup v-model="yiji" :style="{ padding:'50px',width:'60%'}" class="yiji_">
      <div class="yiji">
        <h4 style="text-align: center;margin-bottom:10px">请选择代理分成比</h4>
        <van-radio-group v-model="radio" @change='getyiji'>
          <van-radio :name="item" v-for="(item, index) in for_radio" :key="index">{{item*5}}%</van-radio>
        </van-radio-group>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { getmy_trun_data, getdistributor_edit } from '@/axios/getData'
import { Popup, Radio, Area, RadioGroup, Icon, Toast, CheckboxGroup, Checkbox } from 'vant';
import util from '../utils/utils.js'
export default {
  data(){
    return{
      erji_list:99,
      yiji:false,
      yiji_value:'请选择代理分成比',
      erji:false,
      erji_value:'',
      radio:'',
      data_hu: {},   //  数据编辑
      for_radio: 0,   //  代理分成比  <= 后台返回的分成比例
    }
  },
  components: {
    vanPopup: Popup,
    vanRadio: Radio,
    vanArea: Area,
    vanRadioGroup: RadioGroup,
    vanIcon: Icon,
    vanCheckboxGroup: CheckboxGroup,
    vanCheckbox: Checkbox
  },
  created() {
    this.getdata()
  },
  methods:{
    // 获取一级分成比
    getyiji(a){
      this.data_hu.one_level = a*5
      this.yiji = false
    },
    // 数据提交
    getsetin(){
      if( this.data_hu.username&& this.data_hu.mobile&& this.data_hu.wx_number&& this.data_hu.code_id&& this.data_hu.one_level){

        getdistributor_edit(this.data_hu).then((res)=>{
          if(res.code==200){
            Toast(res.data);
            this.$router.go(-1)
          } else if(res.code==10000){
            Toast(res.msg);
          }
        })
      } else {
        Toast('请完善入驻信息')
      }
    },
    // 获取身份
    getshenfen(){
      this.activeNames = false;
      this.shenfen = this.shenfen_data[this.radio]
    },
    // 变更地址
    changeAddress(val) {
      this.area = val[0].name +'-'+ val[1].name +'-'+ val[2].name
      this.show = false
    },
    async getdata(){
      var res = await getmy_trun_data({user_id: this.$route.query.id})
      console.log(res.data)
      this.data_hu = res.data
      this.radio = parseInt(res.data.one_level/5)
      this.for_radio = parseInt(res.data.fire_one_level/5)
      
    },
  },
  mounted(){

  }
}
</script>

<style scoped lang="scss">
.top{
  padding:50px 15%;
  height:100vh;
  background: #FAFAFA;
}
  .info_wrapper {
    padding: 25px 0;
    box-sizing:border-box;
    border-bottom: 1px solid rgb(238, 238, 238);
    display: flex;
    align-items: center;
    .name{
      width:27%;
      text-align: right;
    }
    .red {
      color: #e62121;
    }
    .weizhi{
      border: 1px solid #DEDEDE;
      margin-left:20px;
      padding:10px;
      box-shadow: 0 0 7px #DEDEDE inset;
      flex:1;
      background: #fff;
    }
    input {
      border: 1px solid #DEDEDE;
      margin-left:20px;
      padding:10px;
      box-shadow: 0 0 7px #DEDEDE inset;
      flex:1;
    }
  }
  input::-webkit-input-placeholder{
    color:#999;
  }
  #shenfen{
    width:50%;
    padding:50px;
    border-radius: 10px;
    .title{
      text-align: center;
      display: inline-block;
      width:100%;
      margin-bottom:20px;
    }
    /deep/ .van-radio{
      margin-bottom:15px;
    }
  }
  #ditu{
    width:101%;
    height:100%;
  }
  .search{
    display: flex;
    padding:5%;
    align-items: center;
    .input{
      width:70%;
      border:1px solid #eee;
      padding:10px;
    }
    .se{
      margin-left:30px;
      padding:10px 30px;
      background: #15BDDB;
      color:#fff;
    }
  }
  .item{
    padding:25px 30px;
    border-bottom:1px solid #eee;
    display: flex;
    align-items: center;
    /deep/ .van-icon{
      margin-right:15px;
    } 
  }
  .submit{
    position: fixed;
    bottom:0;
    // z-index: 1;
    // text-align: center;
    padding:15px 0;
    background: #fff;
    width:100%;
    // border-top:1px solid #eee;
    .btn{
      font-size: 30px;
      width:70%;
      background: #15BDDB;
      color:#fff;
      border-radius: 50px;
      padding:20px 0;
    }
  }
  /deep/ .van-radio{
    margin-bottom:10px;
  }
  /deep/ .van-checkbox{
    margin-bottom:10px;
  }
  .yiji_{
    border-radius: 10px;
  }
  .quanxian{
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center
  }
  .mlr{
    margin:0 30px; 
  }
</style>



